<template>
  <v-container fluid>
    <v-card>
      <v-card-title>
        <span class="text-h5">My Tasks</span>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          outlined
          dense
          class="mx-4"
          style="max-width: 300px"
        ></v-text-field>
        <v-btn color="primary" @click="refreshTasks" :loading="loading">
          <v-icon left>mdi-refresh</v-icon>
          Refresh
        </v-btn>
      </v-card-title>

      <v-card-text>
        <v-row>
          <v-col cols="12" sm="6" md="3">
            <v-select
              v-model="statusFilter"
              :items="statusOptions"
              label="Status"
              outlined
              dense
              clearable
              hide-details
              @change="applyFilters"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="2">
            <v-select
              v-model="sortBy"
              :items="sortOptions"
              label="Sort By"
              outlined
              dense
              hide-details
              @change="applyFilters"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="2">
            <v-select
              v-model="sortDirection"
              :items="[
                { text: 'Ascending', value: 'asc' },
                { text: 'Descending', value: 'desc' },
              ]"
              label="Order"
              outlined
              dense
              hide-details
              @change="applyFilters"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="1">
            <v-btn
              color="secondary"
              outlined
              block
              @click="resetFilters"
              :disabled="!hasActiveFilters"
            >
              Reset
            </v-btn>
          </v-col>
          <!-- Only show the button if at least one ADAC file is selected -->
          <v-col cols="12" sm="6" md="3" v-if="canExpressAccept">
            <v-btn
              color="success"
              :disabled="!canExpressAccept"
              @click="expressAcceptSelected"
              outlined
              block
            >
              <v-icon left>mdi-checkbox-multiple-marked</v-icon>
              ACCEPT ADJUSTER ASSIGNMENT(S)
            </v-btn>
          </v-col>
        </v-row>
        <!-- Show info message if there are ADAC files in the list -->
        <v-alert
          v-if="hasAdacFiles"
          type="info"
          outlined
          dense
          class="mt-2 mb-0"
        >
          You have {{ adacCount }} casefile(s) pending
          <b>ADJUSTER ACCEPTANCE</b>. Select one or more related files and click
          <b>ACCEPT ADJUSTER ASSIGNMENT(S)</b> to accept them in bulk.
        </v-alert>
      </v-card-text>

      <v-data-table
        :headers="headers"
        :items="filteredTasks"
        :search="search"
        :loading="loading"
        :items-per-page="10"
        :footer-props="{
          'items-per-page-options': [10, 20, 50, -1],
          'items-per-page-text': 'Tasks per page',
        }"
        @click:row="navigateToTask"
        class="cursor-pointer"
        show-select
        :item-selectable="isSelectable"
        v-model="selected"
      >
        <template #[`item.id`]="{ item }">
          AA<span
            v-for="(dept, index) in filterDept(item.refType)"
            :key="'dept-' + index"
            >/{{ dept.name || dept.description
            }}<span
              v-for="(subDept, sIndex) in filterSubDept(item.subRefType)"
              :key="'sub-' + sIndex"
              >/{{ subDept.subCode }}</span
            > </span
          >/{{ item.id }}/{{ yearOnly(item.dateOfAssign)
          }}<span
            v-for="branch in filterBranches(item.branch)"
            :key="'br-' + branch.id"
            >/{{ branch.brCode }}</span
          >
        </template>

        <template v-slot:[`item.fileStatus`]="{ item }">
          <v-chip
            small
            :color="getStatusColor(item.fileStatus)"
            text-color="white"
          >
            {{ getStatusName(item.fileStatus) }}
          </v-chip>
        </template>

        <template v-slot:[`item.daysOpen`]="{ item }">
          <v-chip
            small
            :color="getDaysColor(item.daysOpen)"
            text-color="white"
            class="text-no-wrap"
          >
            {{ item.daysOpen }} days
          </v-chip>
        </template>

        <template v-slot:[`item.insurerName`]="{ item }">
          <div>{{ getInsurerName(item.insurer) }}</div>
        </template>

        <template v-slot:no-data>
          <v-alert type="info" text class="text-center my-2" outlined dense>
            <div class="d-flex flex-column align-center py-4">
              <v-icon x-large color="grey lighten-1" class="mb-3">
                mdi-check-all
              </v-icon>
              <span class="text-h6 grey--text text--darken-1">
                No tasks assigned to you
              </span>
              <span class="text-body-2 grey--text">
                Tasks assigned to you will appear here
              </span>
            </div>
          </v-alert>
        </template>

        <template v-slot:no-results>
          <v-alert type="info" text outlined dense class="my-2">
            No matching tasks found for "{{ search }}"
          </v-alert>
        </template>

        <template v-slot:loading>
          <div class="d-flex justify-center align-center pa-4">
            <v-progress-circular
              indeterminate
              color="primary"
            ></v-progress-circular>
            <span class="ml-4">Loading tasks...</span>
          </div>
        </template>
      </v-data-table>
    </v-card>

    <!-- Snackbar for notifications -->
    <v-snackbar
      v-model="snackbar.show"
      :color="snackbar.color"
      :timeout="snackbar.timeout"
      bottom
      rounded="pill"
      elevation="4"
      max-width="400"
    >
      <div class="d-flex align-center px-2">
        <v-icon left>{{ snackbar.icon }}</v-icon>
        <span class="font-weight-medium">{{ snackbar.text }}</span>
        <v-spacer></v-spacer>
        <v-btn
          x-small
          fab
          :color="snackbar.actionColor || 'white'"
          class="ml-2"
          @click="snackbar.show = false"
        >
          <v-icon color="black">mdi-close</v-icon>
        </v-btn>
      </div>
    </v-snackbar>
  </v-container>
</template>

<script>
import CasefileDataService from "../services/CasefileDataService";
import StagesDataService from "../services/StageDataService";
import InsurerDataService from "../services/InsDataService";
import RefTypeDataService from "../services/DeptDataService";
import SubRefTypeDataService from "../services/SubDeptDataService";
import BranchDataService from "../services/BranchDataService";
import moment from "moment";

export default {
  name: "MyTasks",
  data: () => ({
    loading: false,
    tasks: [],
    search: "",
    statusFilter: null,
    sortBy: "daysOpen",
    sortDirection: "desc",
    stages: [],
    headers: [
      { text: "AASB REF", value: "id", width: "180px" },
      { text: "Status", value: "fileStatus", width: "120px" },
      { text: "Insurer", value: "insurerName", width: "180px" },
      { text: "Days Open", value: "daysOpen", width: "100px" },
    ],
    statusOptions: [],
    sortOptions: [
      { text: "Days Open", value: "daysOpen" },
      { text: "Case ID", value: "id" },
      { text: "Status", value: "fileStatus" },
    ],
    snackbar: {
      show: false,
      text: "",
      color: "info",
      timeout: 3000,
      icon: "mdi-information",
      actionColor: "white",
    },
    insurers: [],
    refTypes: [],
    subRefTypes: [],
    branches: [],
    selected: [],
  }),

  computed: {
    currentUser() {
      return this.$store.state.auth.user || null;
    },
    hasActiveFilters() {
      return (
        this.statusFilter ||
        this.search ||
        this.sortBy !== "daysOpen" ||
        this.sortDirection !== "desc"
      );
    },
    filteredTasks() {
      let result = [...this.tasks];

      // Apply status filter
      if (this.statusFilter) {
        result = result.filter((task) => task.fileStatus === this.statusFilter);
      }

      // Apply sorting
      result.sort((a, b) => {
        let comparison = 0;
        switch (this.sortBy) {
          case "daysOpen":
            comparison = a.daysOpen - b.daysOpen;
            break;
          case "id":
            comparison =
              (parseInt(a.id) || 0) - (parseInt(b.id) || 0) ||
              a.id.localeCompare(b.id);
            break;
          case "fileStatus":
            comparison = (a.fileStatus || "").localeCompare(b.fileStatus || "");
            break;
          default:
            comparison = a.daysOpen - b.daysOpen;
        }
        return this.sortDirection === "desc" ? -comparison : comparison;
      });

      return result;
    },
    canExpressAccept() {
      // Only enable and show button if at least one ADAC file is selected
      return (
        this.selected.length > 0 &&
        this.selected.every((task) => task.fileStatus === "ADAC")
      );
    },
    hasAdacFiles() {
      // Show info if there are ADAC files in the filtered list
      return this.filteredTasks.some((task) => task.fileStatus === "ADAC");
    },
    adacCount() {
      return this.filteredTasks.filter((task) => task.fileStatus === "ADAC")
        .length;
    },
  },

  async mounted() {
    this.loading = true;
    try {
      await Promise.all([
        this.retrieveStages(),
        this.retrieveInsurers(),
        this.retrieveRefTypes(),
        this.retrieveSubRefTypes(),
        this.retrieveBranches(),
      ]);
      await this.refreshTasks();
    } catch (error) {
      console.error("Error initializing tasks:", error);
      this.showSnackbar(
        "Error loading tasks. Please try again later.",
        "error"
      );
    } finally {
      this.loading = false;
    }
  },

  methods: {
    async retrieveStages() {
      return this.safeDataFetch(StagesDataService.getAll(), "stages", "stages");
    },
    async retrieveInsurers() {
      return this.safeDataFetch(
        InsurerDataService.getAll(),
        "insurers",
        "insurers"
      );
    },
    async retrieveRefTypes() {
      return this.safeDataFetch(
        RefTypeDataService.getAll(),
        "reference types",
        "refTypes"
      );
    },
    async retrieveSubRefTypes() {
      return this.safeDataFetch(
        SubRefTypeDataService.getAll(),
        "sub reference types",
        "subRefTypes"
      );
    },
    async retrieveBranches() {
      return this.safeDataFetch(
        BranchDataService.getAll(),
        "branches",
        "branches"
      );
    },
    async safeDataFetch(promise, entityName, propertyName) {
      try {
        const response = await promise;
        this[propertyName] = response.data || [];
        return response.data;
      } catch (error) {
        console.error(`Error loading ${entityName}:`, error);
        this[propertyName] = [];
        return [];
      }
    },
    async refreshTasks() {
      this.loading = true;
      try {
        const response = await CasefileDataService.getAll();
        const cases = response.data;

        // Filter cases assigned to current user
        if (!this.currentUser || !this.currentUser.id) {
          this.showSnackbar("User information not available", "warning");
          this.tasks = [];
          return;
        }

        const userTasks = cases
          .filter((c) => {
            // Skip closed or cancelled cases
            if (c.fileStatus === "CLO" || c.fileStatus === "CANC") return false;
            // Include cases where the user is adjuster, editor, or clerk in charge
            return (
              c.adjuster === this.currentUser.id ||
              c.editor === this.currentUser.id ||
              c.clerkInCharge === this.currentUser.id
            );
          })
          .map((c) => this.mapCaseToTask(c));

        this.tasks = userTasks;

        // Update status options from actual data
        this.updateStatusOptions();

        this.showSnackbar(
          `Loaded ${userTasks.length} tasks successfully`,
          "success"
        );
      } catch (error) {
        console.error("Error refreshing tasks:", error);
        this.showSnackbar("Error refreshing tasks", "error");
      } finally {
        this.loading = false;
      }
    },
    mapCaseToTask(c) {
      // Only keep fields needed for table
      return {
        id: c.id,
        prefix: c.prefix || "AA",
        fileStatus: c.fileStatus || "NEW",
        daysOpen: this.calculateDaysOpen(c),
        refType: c.refType,
        subRefType: c.subRefType,
        branch: c.branch,
        dateOfAssign: c.dateOfAssign,
        insurerName: this.getInsurerName(c.insurer),
        insurer: c.insurer,
      };
    },
    calculateDaysOpen(caseFile) {
      // Use earliest assignment date among adjuster/editor/clerk
      let dates = [
        caseFile.dateOfAssign,
        caseFile.dateOfEdi,
        caseFile.dateAssignFormatting,
      ].filter(Boolean);
      let date = dates.length ? dates.sort()[0] : null;
      return date ? Math.max(0, moment().diff(moment(date), "days")) : 0;
    },
    updateStatusOptions() {
      if (!this.stages.length || !this.tasks.length) return;
      this.statusOptions = [
        ...new Set(this.tasks.map((task) => task.fileStatus).filter(Boolean)),
      ].map((code) => {
        const status = this.stages.find((s) => s.stageCode === code);
        return {
          text: status ? status.name : code,
          value: code,
        };
      });
    },
    getDaysColor(daysOpen) {
      if (daysOpen > 30) return "error";
      if (daysOpen > 15) return "warning";
      return "success";
    },
    getStatusName(statusCode) {
      if (!this.stages.length) return statusCode;
      const status = this.stages.find((s) => s.stageCode === statusCode);
      return status ? status.name : statusCode;
    },
    getStatusColor(status) {
      const statusColorMap = {
        NEW: "blue",
        INVE: "orange",
        PEDI: "purple",
        REV: "indigo",
        RET: "deep-orange",
        CANC: "grey",
        CLO: "green",
      };
      return statusColorMap[status] || "primary";
    },
    yearOnly(dateString) {
      return dateString ? moment(dateString).format("YYYY") : "";
    },
    getInsurerName(insurerId) {
      if (!insurerId || !this.insurers.length) return "Unknown";
      const insurer = this.insurers.find((ins) => ins.id === insurerId);
      return insurer ? insurer.name : insurerId;
    },
    filterDept(deptId) {
      if (!deptId || !this.refTypes.length) return [];
      return this.refTypes.filter((dept) => dept.id === deptId);
    },
    filterSubDept(subDeptId) {
      if (!subDeptId || !this.subRefTypes.length) return [];
      return this.subRefTypes.filter((subDept) => subDept.id === subDeptId);
    },
    filterBranches(branchId) {
      if (!branchId || !this.branches.length) return [];
      return this.branches.filter((branch) => branch.id === branchId);
    },
    isSelectable(item) {
      // Only allow selection for ADAC files
      return item.fileStatus === "ADAC";
    },
    async expressAcceptSelected() {
      if (!this.canExpressAccept) return;
      this.loading = true;
      try {
        // Accept all selected ADAC files using same logic as acceptAssignment.vue
        const promises = this.selected.map((task) =>
          CasefileDataService.update(task.id, {
            ...task,
            fileStatus: "INVE",
            dateStartInv: moment().format(),
          })
        );
        await Promise.all(promises);
        this.showSnackbar(
          `Accepted ${this.selected.length} assignment(s) successfully`,
          "success"
        );
        this.selected = [];
        await this.refreshTasks();
      } catch (error) {
        this.showSnackbar("Error accepting assignments", "error");
      } finally {
        this.loading = false;
      }
    },
    applyFilters() {
      // Triggers recomputation of filteredTasks
    },
    showSnackbar(text, color = "info") {
      let icon = "mdi-information";
      let actionColor = "white";
      if (color === "error") icon = "mdi-alert-circle";
      else if (color === "success") icon = "mdi-check-circle";
      else if (color === "warning") icon = "mdi-alert";
      this.snackbar = {
        show: true,
        text,
        color,
        timeout: 3000,
        icon,
        actionColor,
      };
    },
    navigateToTask(task) {
      this.$router.push({
        name: "vuexFile",
        params: { id: task.id },
      });
    },
    resetFilters() {
      this.statusFilter = null;
      this.search = "";
      this.sortBy = "daysOpen";
      this.sortDirection = "desc";
    },
  },
};
</script>

<style scoped>
.cursor-pointer >>> tbody tr {
  cursor: pointer;
}
.cursor-pointer >>> tbody tr:hover {
  background-color: rgba(0, 0, 0, 0.04) !important;
}
.v-data-table >>> th {
  font-weight: 600 !important;
  color: rgba(0, 0, 0, 0.6) !important;
  white-space: nowrap;
}
</style>
